* {
  padding: 0px;
  margin: 0px;
  box-sizing: border-box;
}

body {
  background: linear-gradient(to bottom right, #007fc2, #00d6ee);
}

.container {
  width: 1200px;
  padding: 0 10px;
  margin: 0 auto;
}

.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
}

h1 {
  font-family: sans-serif;
  font-weight: 700;
  color: #fff;
  font-size: 50px;
}

.main-header {
  padding-top: 100px;
  padding-bottom: 50px;
}

.clearfix:before,
.clearfix:after {
  content: '';
  display: table;
  clear: both;
}

.unlist {
  list-style-type: none;
}

.card {
  padding: 50px 30px 60px 30px;
  height: 500px;
  width: 350px;
  float: left;
  background: #fff;
  border-radius: 2%;
  margin-left: 15px;
  margin-bottom: 45px;
}

.photo {
  border-radius: 50%;
}

h3 {
  padding-bottom: 10px;
  padding-top: 20px;
  font-size: 35px;
}

.feedback {
  font-size: 17px;
  padding-bottom: 25px;
}

.date {
  display: block;
  margin-top: 10px;
  font-size: 20px;
  color: grey;
  font-style: italic;
}